import React from 'react'
import {Box,Image, Text} from "@chakra-ui/react";
const About = () => {
  return (
    <Box id="about" display={'flex'} color={'whiteAlpha.900'} 
    fontFamily={'Mukta,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif'}
    padding={['80px','80px','100px']}flexDirection={['column','row','row','row']} alignItems="center" justifyContent={'space-evenly'} >
        <Box  width={['100%','70%','30%']} alignItems="center" display={'flex'} flexDirection='column'>
            <Image  src="https://avatars.githubusercontent.com/u/107459781?v=4" height={["200px","200px","240px"]} width={['200px','200px','240px']} borderRadius="50%"/>
            <Text fontWeight="700" fontSize={['20px','20px','25px']}>G Prasad Bhat</Text>
            <Text fontWeight={'600'} fontSize={['14px','14px','16px']}><i>Full Stack Web Developer</i></Text>
        </Box>
        <Box  width={['100%','80%','50%']}>
              <Text fontWeight={'600'} fontSize={['14px','16px','24px']} textAlign="center">An enthusiastic software developer who spent 1200+ hrs on coding and solved 100+ DSA problems with massive training on project presentation by Masai School and can fit to work at any working environment.</Text>
        </Box>
      </Box>
  )
}

export default About